import React,{Component} from "react"
import PropTypes from "prop-types"
export default  class Dome1 extends Component{
	          // Dome 
              static propTypes={
                name:PropTypes.string
            }
                //
                static defaultProps={
                    sex:"",
                    name:""
                } 
   
    state =  {'arr':['A','B','C','D','F'],'ishot':false}
    changeishot=(()=>{
        this.setState({
            ishot:!this.state.ishot
        })
    })



    // render() DOM , DOM
        render(){
   
     let todos = this.props
    console.log(this.props)
            return(
                <div>
                    <button onClick={this.changeishot}>{this.props.name}ishot{this.state.ishot?'true':"false"}</button>
                    <ul>
                        {
                       this.state.arr.map((item,index)=>{
                           return <li key={index}>{this.state.ishot?'123':item}</li>
                        })
                        }
                    </ul>
			    </div>  
            )
        }
}